import React from "react"
// import { Link } from "gatsby"

import Layout from "../components/layout"
// import Image from "../components/image"
import SEO from "../components/seo"
import HomeTitle from '../components/homepage/homeTitle'
import VideoCaption from '../components/videoCaption/videoCaption'
import Introduction from '../components/intro/introduction'
import Services from '../components/services/services'
import Contact from '../components/contact/contactUs'

import captionVideo from '../assets/video/homepage.mp4'

const IndexPage = () => (
  <Layout>
    <SEO 
      title="Bem Vindo!" 
      description="Elevamos o seu projeto ao próximo nível com uma melhor experiência digital. Desenvolvimento Web, Desenvolvimento Gráfico e muito mais. Encontre o seu caminho."
    />
    <HomeTitle title="find your path." leading="@pathfinderdesigns" />
    <VideoCaption video={captionVideo} caption="Elevamos o seu projeto ao próximo nível com uma melhor experiência digital." />
    <Introduction />
    <Services />
    <Contact />
  </Layout>
)

export default IndexPage
