import React, { useEffect } from 'react'

import { IntroductionContainer } from '../../styles/introductionStyles'
import { Container, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

import { motion } from 'framer-motion'

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'

const Introduction = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])
    
    
    
    return(
        <Container>
            <IntroductionContainer ref={contentRef} variants={parent} initial='hidden' animate={animation}>
                <VerticalPadding>
                    <DefaultSizes black>
                        <motion.h1 variants={child}>Uma identidade.</motion.h1>
                        <motion.p variants={child}>A sua marca, grande ou pequena, tem um super poder. Tudo o que ela faz tem o potencial de criar boas experiências e sensações na mente dos seus clientes. Todas as ações, por mais pequenas que sejam, têm impacto nessa perceção. Assim, é importante que tais ações estejam cobertas com uma boa identidade. A mesma aparência, a mesma tonalidade e o mesmo tom de voz. Uma boa identidade, pode tornar a sua marca, algo especial. Vamos criá-la juntos?</motion.p>
                    </DefaultSizes>
                </VerticalPadding>
            </IntroductionContainer>
            
        </Container>
        
    )
}

export default Introduction;
