
import React, {useEffect} from 'react'
import {motion} from 'framer-motion'

import { Image, BriefContainer, BriefBackground } from '../../styles/videoCaptionStyles'
import { Container, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'

const ImageCaption = (props) => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <BriefBackground>
            <Image>
                <img src={props.image} alt =""/>
            </Image >
            <Container 
                ref={contentRef} 
                animate={animation} 
                initial='hidden' 
                variants={parent}
            >
               <VerticalPadding>
                   <BriefContainer>
                    <DefaultSizes >
                        <motion.h1 variants={child}>{props.caption}</motion.h1>
                        <motion.p variants={child}>{props.desc}</motion.p>
                    </DefaultSizes>
                </BriefContainer>
               </VerticalPadding>  
            </Container>
        </BriefBackground>
        
    )
}

export default ImageCaption